$color-primary: white;
$color-secondary: rgb(197, 136, 136); //rose
$color-tertiary: rgba(204, 226, 175, 0.6); //vert
$color-quaternary: rgba(250, 250, 173, 0.6); //jaune
$color-quinary: rgba(179, 179, 179, 0.199); //gris
$color-senary: rgba(0, 0, 255, 0.015); //bleu

$color-agrement-1: rgb(86, 86, 226);
$color-agrement-2: rgb(145, 145, 224);

$color-link-1: darkblue;
$color-link-2: rgb(92, 92, 255);

$text-shadow-1: 0px 1px 2px rgba(0, 0, 0, 0.2);
$text-shadow-2: 0px 1px 2px rgba(0, 0, 0, 0.4);

$box-shadow-1: #00000066 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px,
  rgba(0, 0, 0, 0.2) 0px -3px 0px inset;

$box-shadow-2: rgba(255, 255, 255, 0.582) 0px 3px 0px inset,
  #00000085 0px 7px 5px inset;

$box-shadow-3: #0000007a 0px 3px 5px, rgba(0, 0, 0, 0.3) 0px 14px 23px -3px,
  rgba(0, 0, 0, 0.22) 0px -6px 0px inset;

* {
  font-family: "Roboto", sans-serif;

  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  //text-shadow: $text-shadow-1;
}

body {
  //margin: 0px auto;
  //max-width: 1296px;
 // min-width: 350px;
  background-color: $color-primary;
}
.box_content_center {
  margin: 0px auto;
  max-width: 1296px;
  min-width: 350px;
}

li {
  list-style-type: none;
}
a {
  text-decoration: none;
  white-space: nowrap;
  cursor: pointer;
}
p {
  text-align: justify;
}
