.footer {
  // border: 1px solid black;
  //background-color:rgb(94, 94, 94);

  box-shadow: rgba(0, 0, 0, 0.459) 0px -2px 4px;
  //margin-top: 25px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  //flex-direction: column;
  background-image: url("../../assets/bannieresite.webp");
  background-attachment: fixed;
  background-size: cover;
  background-position: center;

  .footer_debut {
    //  flex-grow: 1;
    margin: 5px auto;
    padding: 5px 20px;
    .info_site {
      width: max-content;
      margin: 5px auto;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      color: rgb(230, 230, 230);
      text-shadow: 1px 1px 2px black, 0 0 20px rgb(0, 133, 133),
        0 0 5px rgb(0, 80, 80);
    }
  }
  .footer_milieu {
    flex-grow: 1;
    margin: 5px auto;
    padding: 5px 20px;
    .liste_lien {
      width: max-content;
      margin: 5px auto;
      min-height: 50px;
      min-width: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgba(71, 71, 71, 0.575);
      border: 1px solid rgba(255, 255, 255, 0.404);
      border-radius: 15px;
      .icon_rond_lien {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(255, 255, 255, 0.527);
        width: 35px;
        height: 35px;
        margin: 10px;
        border-radius: 20px;
        transition: background-color 0.2s;
        &:hover {
          background-color: rgba(255, 255, 255, 1);
        }
        .icon_lien {
          font-size: 22px;
          color: rgb(10, 102, 194);
        }
      }
    }
  }
  .footer_fin {
    //  margin: 0px 0px 0px 20px;
  }
}
