
.loadSpinner {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 50;
  background-color: rgba(255, 255, 255, 0.808);


  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  & i {
    margin-top: 20px;
    font-size: 5rem;

    // technique pour utiliser le gradient sur les icons fontawesome
    background: radial-gradient(
      ellipse at right center,
      lighten(rgb(0, 133, 133), 30%),
      rgb(0, 133, 133)
    );
    background-clip: text;
    -moz-background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    display: initial;
  }
  & p {
    font-weight: bold;
    font-size: 1.5rem;
    margin-top: 10px;
    color: darken(rgb(0, 133, 133), 10%);
  }
}
.hide{
    display: none;
}
