.serviceIntro {
  margin: 10px;
  //position: relative;
  h2 {
    margin: 25px 10px;
    padding: 10px 10px 10px 10px;
    //padding: 20px 10px;
    text-align: center;
     color: rgba(0, 100, 100, 1);
   // color: rgba(109, 57, 139, 1);
    // color: $color-agrement-1;
    text-shadow: $text-shadow-2;
    font-size: 2.2rem;
    //border: 1px solid black;
    text-decoration: underline;
    // background-color: rgba(0, 133, 133, 0.171);
  }

  p {
    margin: 45px 10px;
    padding-left: 10px;
    text-align: left;
    max-width: 500px;
    color: rgb(0, 0, 61);
    border-left: 4px solid rgb(145, 145, 224);
  }
  .servicesConteneur {
    // position: absolute;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    ul {
      //  background-color: rgb(62, 223, 29);
      padding: 10px 10px 10px 20px;
      margin: 2px;
      max-width: 295px;
      // box-shadow: $box-shadow-1;
      //  border-radius: 12px;
      position: relative;
      border: 1px solid rgba(255, 255, 255, 0.089);
      visibility: hidden;
      opacity: 0;
      background-color: rgb(0, 133, 133);
      // background-color: rgba(109, 57, 139, 1);
      box-shadow: none;
      top: 4px;
      transition: visibility 0s, opacity 1.5s ease-out, background-color 0.5s,
        box-shadow 1.2s, top 0.5s;
      box-shadow: rgba(0, 0, 0, 0.466) 0px 2px 4px,
        rgba(0, 0, 0, 0.322) 0px 4px 10px;
      li {
        margin: 5px 10px;
        list-style-type: disc;
        color: white;
        text-shadow: 1px 1px 2px black;
      }
    }
    .ulAnimation1 {
      visibility: visible;
      opacity: 1;
      background-color: rgba(0, 133, 133, 0.9);
      //  background-color: rgba(109, 57, 139, 1);
      //   box-shadow: $box-shadow-1;
      top: 0px;
    }
    .ulAnimation2 {
      visibility: visible;
      opacity: 1;
      background-color: rgba(0, 161, 160, 0.9);
      //   background-color: rgba(109, 57, 139, 0.9);
      //  box-shadow: $box-shadow-1;
      top: 0px;
      transition-delay: 400ms;
    }
    .ulAnimation3 {
      visibility: visible;
      opacity: 1;
      background-color: rgba(0, 184, 181, 0.9);
      // background-color: rgba(109, 57, 139, 0.8);
      //   box-shadow: $box-shadow-1;
      top: 0px;
      transition-delay: 800ms;
    }
  }
}
