.bandeauPlatforme {
   
 // background-color: rgb(250, 250, 173);

  background: linear-gradient(rgb(255, 255, 255) , rgb(250, 250, 173));


  .bandeauPlatforme_box {

    .bandeauPlatforme_opacity {
          padding: 20px 0px ;
       // background-color: rgba(255, 255, 255, 0.15);  
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;   
      .bandeauPlatforme_contenu {       
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        .bandeauPlatforme_cercle {
          display: flex;
          margin: 5px 5px;
          border-radius: 40px;
          width: 80px;
          height: 80px;
          justify-content: center;
          align-items: center;
         // background-color: rgb(205, 218, 188);
          background-color: rgb(0, 133, 133);
          border: 2px solid white;
          box-shadow: rgba(0, 0, 0, 0.596) 0px 2px 4px,rgba(0, 0, 0, 0.466) 0px 4px 10px;
          i {
            font-size: 2rem;
            color: white;
          }
        }
      }
    }
  }
}
