//animations
@keyframes chachermenu {
  0% {
    opacity: 1;
    visibility: visible;
  }
  25% {
  }
  50% {
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    visibility: hidden;
    //  z-index: -1;
  }
}

.navigation {
  position: static;
  margin: 10px 0px 0px 0px;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  //width: 100px;
  flex-direction: row;
  //align-items: flex-start;
  //background: url(../../assets/navbar1.webp) center/cover;
  //border: 1px solid black;
  background-color:rgba(255, 255, 255, 0.9);
 // background-color: #b0e4fcd2;
 
  border: none;
 
  //box-shadow: none;
 // box-shadow: $box-shadow-1;
 //margin: 20px auto;
 //max-width: 1296px;
 //min-width: 350px;

  z-index: 20;
  .logo {
    display: flex;
    justify-content: left;
    align-items: center;
    margin: 0px 5px;
    cursor: pointer;

    border-bottom: 1px solid rgba(116, 47, 47, 0.1);

    .menu_ico_rond {
      width: 30px;
      height: 30px;
      border-radius: 15px;
      background: rgba(255, 255, 255, 0);

      display: flex;
      justify-content: center;
      align-items: center;
      i {
        margin: 0px;
        font-size: 1.2rem;
        color: darkblue;
        transition: transform 0.2s;
        transform: rotate(90deg);
      }
    }
    p {
      padding: 0px 5px;
      display: initial;
    }
  }
  .logo_fermer {
    display: flex;
    border: none;
    .menu_ico_rond {
      background: rgba(255, 255, 255, 0.95);
      border: 1px solid rgba(116, 47, 47, 0.664);
      box-shadow: 2px 2px 4px gray;

      i {
        transform: rotate(0deg);
      }
    }
    p {
      display: none;
    }
  }
  .logo_cacher {
    display: none;
  }

  .nav_menu {
    nav {
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: center;
      align-items: flex-start;
    }
    .nav_colonne {
      flex-direction: column;
    }
    .nav_cacher {
      display: none;
    }
  }
}

.navigation_fixed {
  padding: 0px;
  position: fixed;
  top: 40%;
  // display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0);
  box-shadow:none;
}
.navigation_cacher {
  // display: none;
  visibility: hidden;
  opacity: 1;
  border-radius: 0px;
  border: none;
  box-shadow: none;
  //  z-index: 1;
  animation-name: chachermenu;
  animation-duration: 3s;
}
.navigation_afficher {
  visibility: visible;
  opacity: 1;
}
.navigation_couleur {
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 2px;
  border: 1px solid rgba(116, 47, 47, 0.664);
  box-shadow: 2px 2px 4px gray;
}
