@keyframes carteanim {
  0% {
    box-shadow: none;
    transform: scale(1);
  }
  25% {
    box-shadow: $box-shadow-2;
    transform: scale(0.98);
  }
  50% {
  }
  75% {
    transform: scale(0.98);
    box-shadow: $box-shadow-2;
  }
  100% {
    box-shadow: none;
    transform: scale(1);
  }
}

