.presDetails {
  //  box-shadow:$box-shadow-1;
  position: relative;
  //margin: 10px;
  padding: 20px;
  background-color: none;
 // border-radius: 6px;
  z-index: 2;
  overflow: hidden;
  transition: transform 1s 0.5s;
  //right: -105%;

  //border: 1px solid black;

  grid-column: 2;
  grid-row: 1;

  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
  flex-wrap: wrap;

  img {
    position: absolute;
    top: 5%;
    left: -5px;
    // width: 100%;
    height: 90%;
    object-fit: cover;
    opacity: 0.5;
    z-index: -1;
  }
  p {
     margin-top: 10px;
     margin-right: 25px;
    text-align: justify;
    //text-indent: 50px;
    //letter-spacing: 0rem;
    line-height: 1.3rem;
    // max-width: 800px;
    color: rgb(0, 0, 61);


  }
  .presDetails_Intro {
    text-indent: 50px;
  }
}
.presDetailsAnim {
  transform: translateX(-100%);
}
