.presFiche {
  // box-shadow:$box-shadow-2;
  position: relative;
  // margin: 10px;
  padding: 20px;
  // margin-right: 25px;
  background-color: none;
  // background-color: rgba(250, 250, 173, 0.9);
  //border-radius: 6px;
  z-index: 2;
  overflow: hidden;
  transition: transform 1s 0.5s;
  //border: 1px solid black;

  grid-column: 1;
  grid-row: 1;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;

  img {
    position: absolute;
    top: 5%;
    right: -10px;
    // width: 100%;
    height: 90%;
    object-fit: cover;
    opacity: 0.5;
    z-index: -1;
  }
  .presFichePhoto {
    width: 200px;
    height: 200px;
    min-width: 200px;
    margin: 0px auto;

    background-image: url("../../../assets/NK.webp");

    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }
  .presFicheInfo {
    // border:1px solid black;
    margin: 10px;
  }
  p,
  ul,
  h3 {
    padding-top: 10px;
    padding-left: 10px;
    margin-right: 10px;

    line-height: 1.3rem;
    // max-width: 800px;
    color: rgb(0, 0, 61);
    border-left: 4px solid rgb(145, 145, 224);
  }
  ul {
    padding-top: 1px;
  }

  li {
    margin-left: 20px;
    margin-top: 1px;
    list-style-type: " ⁕ "; //" > ";⋙
  }
}

.presFicheAnim {
  transform: translateX(-100%);
}
