.intro {
  padding: 0px;
  margin-top: 0px;
  margin-bottom: 0px;
  //background-color: $color-tertiary;

  background-image: url("../../assets/reunionprojet.webp");

  background-attachment: fixed;
  background-size: cover;
  background-position: top;

  z-index: 0;
  overflow: hidden;

  .intro_box {
    .intro_opacity {
      background-color: rgba(255, 255, 255, 0.308);
      z-index: 1;

      .intro_opacity_espace1 {
        padding: 35px 50px;
        // border: 1px solid black;
      }
      .intro_opacity_espace2 {
        padding: 10px 50px;
        // border: 1px solid black;
      }

      p {
        padding: 20px 50px;

        text-align: justify;
        text-indent: 50px;
        letter-spacing: 1px;
        line-height: 1.3rem;
        //
        max-width: 800px;
        // color: rgb(0, 0, 61);
        color: white;
        // background-color: rgb(0, 0, 0);
        background-color: rgba(0, 0, 0, 0.55);
        z-index: 4;
        opacity: 1;
      }

      .intro_Block1 {
        position: relative;
        z-index: 3;
        .triangle1 {
          position: absolute;
          z-index: 2;
          height: 90%;
          //max-height: 150px;
          object-fit: cover;

          bottom: 10px;

          left: -1px;
          opacity: 0.7;
          transition: left 1.5s, transform 1.5s 1.5s, opacity 1.5s;
        }
        .triangle1_anim {
          left: -50%;
          transform: rotateZ(14deg);
          opacity: 0;
        }
        .intro_p1 {
          position: relative;
          margin: 10px auto 10px 10px;
          top: 0px;
          transition: transform 1.5s 1.5s, opacity 1.5s;
        }
        .intro_p1Anim {
          transform: translateY(20px);
          opacity: 0;
          //  top: 20px;
        }
      }

      .intro_Block2 {
        position: relative;
        z-index: 3;
        .triangle2 {
          position: absolute;
          z-index: 2;
          height: 90%;
          // max-height: 150px;
          object-fit: cover;
          bottom: 10px;

          right: -1px;
          opacity: 0.7;
          transition: right 1.5s, transform 1.5s 1.5s, opacity 1.5s;
        }
        .triangle2_anim {
          transform: rotateZ(-14deg);
          right: -50%;
          opacity: 0;
        }
        .intro_p2 {
          position: relative;
          margin: 10px 10px 10px auto;

          top: 0px;
          transition: transform 1.5s 1.5s, opacity 1.5s;
        }
        .intro_p2Anim {
          transform: translateY(20px);
          opacity: 0;
          //top: 20px;
        }
      }
    }
  }
}
