.sousmenu {
  margin: 0px 0px 0px 30px;
  padding: 5px;

  border-radius: 0px;
  border: none;

  font-size: 1rem;
  
  .nav-link {
    margin: 0px;
    padding: 0px;
    position: relative;
    color: $color-link-1;

    &:hover {
      color: teal;
    }

    i {
      margin-right: 7px;
    }
    .rotation90deg {
      transform: rotate(90deg);
    }
  }
  .nav-active {
    color: $color-link-2;
  }
}
.sousmenu_fixed {
  padding: 7px;
  border-left: 1px solid rgba(116, 47, 47, 0.1);
  display: block;
  font-size: 0.9rem;
}
.cacher_nonFixe {
  display: none;
}
