 /*background-image: url(../../assets/navbar1.webp);

  //background-attachment: fixed;
  background-size: cover;
   background-position: center;*/


  .bandeauLangages_box {
    .bandeauLangages_opacity {
      padding: 20px 0px;
      //background-color: rgba(255, 255, 255, 0.15);
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      .bandeauLangages_contenu {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        .bandeauLangages_cercle {
          display: flex;
          margin: 5px 5px;
          border-radius: 40px;
          width: 80px;
          height: 80px;
          justify-content: center;
          align-items: center;
          // background-color: rgb(205, 218, 188);
          background-color: rgb(250, 250, 173);
          color: rgb(0, 0, 61);
          text-shadow: rgba(0, 0, 0, 0.349) 0px 1px 2px;
          font-size: 1.3rem;
          border: 2px solid white;
          box-shadow: rgba(0, 0, 0, 0.596) 0px 2px 4px,rgba(0, 0, 0, 0.466) 0px 4px 10px 1px;
        }
      }
    }
  }
